.mdl-tooltip {
  transform: scale(0);
  transform-origin: top center;
  background: rgba(0,0,0, 0.9);
  border-radius: 2px;
  color: rgb(255,255,255);
  display: none;
  font-size: 10px;
  line-height: 14px;
  max-width: 170px;
  padding: 4px 8px;
  position: absolute;
  text-align: center; }

.mdl-tooltip.is-active {
  display: inline-block;
  animation: pulse 200ms cubic-bezier(0, 0, 0.2, 1) forwards; }

.mdl-tooltip--large {
  line-height: 14px;
  font-size: 14px;
  padding: 15px 16px; }

@-webkit-keyframes pulse {
  0% {
    transform: scale(0);
    opacity: 0; }

  50% {
    transform: scale(0.99); }

  100% {
    transform: scale(1);
    opacity: 1;
    visibility: visible; } }

body {
  margin: 0px; }

.styleguide-demo h1 {
  margin: 48px 24px 0 24px; }

.styleguide-demo h1:after {
  content: '';
  display: block;
  width: 100%;
  border-bottom: 1px solid rgba(0, 0, 0, 0.5);
  margin-top: 24px; }

.styleguide-demo {
  opacity: 0;
  transition: opacity 0.6s ease; }

.styleguide-masthead {
  height: 256px;
  background: rgb(33,33,33);
  padding: 115px 16px 0; }

.styleguide-container {
  position: relative;
  max-width: 960px;
  width: 100%; }

.styleguide-title {
  color: #fff;
  bottom: auto;
  position: relative;
  font-size: 56px;
  font-weight: 300;
  line-height: 1;
  letter-spacing: -0.02em; }
  .styleguide-title:after {
    border-bottom: 0px; }
  .styleguide-title span {
    font-weight: 300; }

.mdl-styleguide .mdl-layout__drawer .mdl-navigation__link {
  padding: 10px 24px; }

.demosLoaded .styleguide-demo {
  opacity: 1; }

iframe {
  display: block;
  width: 100%;
  border: none; }

iframe.heightSet {
  overflow: hidden; }

.demo-wrapper {
  margin: 24px; }
  .demo-wrapper iframe {
    border: 1px solid rgba(0, 0, 0, 0.5); }

